import s from "./UserCard.module.css";
import {NavLink} from "react-router-dom";
import React from "react";
import {followTC, unFollowTC, UsersType} from "../../../redux/users-reducer";
import {useDispatch} from "react-redux";
import {useAppSelector} from "../../../redux/redux-store";
import {toggleFollowingSelector} from "../selectors/usersContainer-selector";

type PropsType = {
    userInfo: UsersType
}
export const UserCard = React.memo((props: PropsType) => {
    const dispatch = useDispatch();
    const toggleFollowing = useAppSelector(toggleFollowingSelector);
    const followHandler = (userId: number|null) => {
        if(userId){
            dispatch(followTC(userId))
        }

    }
    const unFollowHandler = (userId: number|null) => {
        if(userId){
            dispatch(unFollowTC(userId))
        }

    }
    function truncateString(str:string) {
        return str.length > 35?str.substring(0, 35) + '...':str
    }
    return <>
            <span className={s.container_img}>
                    <div>
                        <NavLink to={`/profile/${props.userInfo.id}`}>
                            <img
                                src={props.userInfo.photos.small !== null ? props.userInfo.photos.small : "data:image/png;base64,"}
                                alt={"photo user"} className={s.userPhoto}/>
                        </NavLink>
                    </div>
                    <div>
                    {props.userInfo.followed
                        ? <button onClick={() => {
                            unFollowHandler(props.userInfo.id)
                        }} disabled={toggleFollowing.some(id => id === props.userInfo.id)}>Unfollow</button>
                        : <button onClick={() => {
                            followHandler(props.userInfo.id)
                        }} disabled={toggleFollowing.some(id => id === props.userInfo.id)}>Follow</button>
                    }
                    </div>
            </span>
            <span className={s.nameLocation_Container}>
                    <span className={s.userContainer_name}>
                        <div>{props.userInfo.name}</div>
                        <div>{props.userInfo.status !==null?truncateString(props.userInfo.status):""}</div>
                    </span>
                    <span className={s.userContainer_location}>
                        <div>No country</div>
                        <div>No city</div>
                    </span>
            </span>
    </>
})